import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'app-group-member',
  templateUrl: './group-member.component.html',
  styleUrls: ['./group-member.component.css']
})
export class GroupMemberComponent implements OnInit {
  @Input() avatar: string;
  @Input() name: string;
  @Input() own: string;
  // @Output() removeMember: EventEmitter<any> = new EventEmitter();
  constructor(private modalService: NgbModal) { }

  ngOnInit(): void {
  }

  openRemoveMember(contentRemoveMember) {
    this.modalService.open(contentRemoveMember, { centered: true, size: 'sm' });
  }

  getUserSession() {
    return localStorage.getItem("user");
  }
  
  // remove(id: number) {
  //   this.removeMember.emit(id);
  // }
}
