import { Injectable } from '@angular/core';
import { Observable, Subject } from 'rxjs';
import { ChatDTO } from '../model/ChatDTO';
import { ChatMessageDTO } from '../model/ChatMessageDTO';
import { WebSocketService } from './web-socket.service';

@Injectable({
  providedIn: 'root',
})
export class ChatService {
  private selectedChat: ChatDTO;
  private subject = new Subject<any>();
  constructor(public webSocketService: WebSocketService) {}

  private getSendMessage(to: string, type: string, mes: string) {
    return {
      action: 'onchat',
      data: {
        event: 'SEND_CHAT',
        data: {
          type: type,
          to: to,
          mes: mes,
        },
      },
    };
  }

  private getUserListMessage() {
    return {
      action: 'onchat',
      data: {
        event: 'GET_USER_LIST',
      },
    };
  }

  async getUserList() {
    await this.webSocketService.sendMessage(
      JSON.stringify(this.getUserListMessage())
    );
    return new Promise((resolve) => {
      const res = JSON.parse(this.webSocketService.res);
      console.log("Alo",res)
      resolve(res);
    });
  }

  public sendMessage(to: string, isGroup: boolean, mes: string) {
    const type = isGroup ? 'room' : 'people';
    this.webSocketService.sendChatMessage(
      JSON.stringify(this.getSendMessage(to, type, mes))
    );
    const chatMessage: ChatMessageDTO = {
      sender: localStorage.getItem('user'),
      receiver: to,
      message: mes,
      time: new Date().toLocaleString('es-ES'),
      unRead: false,
      position: '',
    };
    if (
      chatMessage.sender !== to ||
      (chatMessage.sender === to && isGroup === true)
    ) {
      this.webSocketService.addChatMessage(chatMessage, true, isGroup);
    }
  }

  public getListChatMessage(chatDTO: ChatDTO): ChatMessageDTO[] {
    let listMesDTO: ChatMessageDTO[];
    const chatDTOs: ChatDTO[] = this.webSocketService.getListChats();
    if (chatDTOs !== undefined) {
      for (const element of chatDTOs)
        if (
          element.name === chatDTO.name &&
          element.isGroup === chatDTO.isGroup
        ) {
          listMesDTO = element.messages;
          this.setPosition(listMesDTO);
        }
      return listMesDTO;
    }
  }

  public getListChat(): ChatDTO[] {
    return this.webSocketService.getListChats();
  }

  public addChat(chatDTO: ChatDTO): void {
    this.webSocketService.getListChats().unshift(chatDTO);
  }

  public setSelectedChat(chatDTO: ChatDTO) {
    this.selectedChat = chatDTO;
    this.subject.next(this.selectedChat);
  }

  public getSelectChat(): Observable<any> {
    return this.subject.asObservable();
  }

  public checkChatInChatList(name: string, isGroup: boolean): ChatDTO {
    let result: ChatDTO = null;
    this.getListChat().forEach((element) => {
      if (
        element.name.toLowerCase() === name.toLowerCase() &&
        element.isGroup === isGroup
      ) {
        result = element;
        return;
      }
    });
    return result;
  }

  public clearChatSelected() {
    this.getListChat().forEach((element) => {
      element.isSelect = false;
    });
  }

  setPosition(listMessagesDTO: ChatMessageDTO[]) {
    for (let i = 0; i < listMessagesDTO.length; i++) {
      const eBefore = listMessagesDTO[i - 1];
      const element = listMessagesDTO[i];
      const eAfter = listMessagesDTO[i + 1];
      if (
        eBefore === undefined ||
        eBefore.sender !== element.sender ||
        this.compareDate(eBefore.time, element.time) > 30000
      ) {
        if (eAfter !== undefined) {
          if (
            element.sender !== eAfter.sender ||
            this.compareDate(element.time, eAfter.time) > 30000
          ) {
            element.position = 'single';
          } else if (
            element.sender === eAfter.sender &&
            this.compareDate(element.time, eAfter.time) <= 30000
          ) {
            element.position = 'start';
          }
        } else {
          element.position = 'single';
        }
      } else if (
        eBefore !== undefined &&
        (eBefore.sender === element.sender ||
          this.compareDate(eBefore.time, element.time) <= 30000)
      ) {
        if (eAfter !== undefined) {
          if (
            element.sender === eAfter.sender &&
            this.compareDate(element.time, eAfter.time) <= 30000
          ) {
            element.position = 'between';
          } else if (
            element.sender !== eAfter.sender ||
            this.compareDate(element.time, eAfter.time) > 30000
          ) {
            element.position = 'end';
          }
        } else {
          element.position = 'end';
        }
      }
    }
  }
  compareDate(date1: string, date2: string): number {
    return this.toDate(date2).getTime() - this.toDate(date1).getTime();
  }

  toDate(dateString: string) {
    const temp: string[] = dateString.split(' ');
    const ntn: string[] = temp[0].split('/');
    const gpg: string[] = temp[1].split(':');
    return new Date(
      Number(ntn[0]),
      Number(ntn[1]),
      Number(ntn[2]),
      Number(gpg[0]),
      Number(gpg[1]),
      Number(gpg[2])
    );
  }
}
