import { Injectable } from '@angular/core';
import { ChatDTO } from '../model/ChatDTO';
import { WebSocketService } from './web-socket.service';

@Injectable({
  providedIn: 'root'
})
export class CheckUserService {

  constructor(public webSocketService: WebSocketService) { }

  async checkUser(username: String) {
    await this.webSocketService.sendMessage(
      JSON.stringify({
        action: "onchat",
        data: {
          event: "CHECK_USER",
          data: {
            user: username
          }
        }
      })
    );
    return new Promise((resolve) => {
      const res = JSON.parse(this.webSocketService.res);
      resolve(res.data.status);
    });
  }


  async updateStatus() {
    const chatDTOs: ChatDTO[] = this.webSocketService.getListChats();
    if (chatDTOs !== undefined) {
      for (const chat of this.webSocketService.getListChats()) {
        let status;
        if (chat.isGroup) {
          for (const member of chat.members) {
            if (localStorage.getItem("user") === member) continue;
            const s = await this.checkUser(member);
            if (s) {
              status = true;
              break;
            }
          }
        } else
          status = await this.checkUser(chat.name) ? true : false;

        chat.status = status;
      }
    }
  }
}
