import { Component, Input, OnInit, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-member-choose',
  templateUrl: './member-choose.component.html',
  styleUrls: ['./member-choose.component.css']
})
export class MemberChooseComponent implements OnInit {

  @Input() avatar: string;
  @Input() name: string;
  @Input() isSelect: boolean;
  @Input() id: number;
  @Output() deleteMember: EventEmitter<number> = new EventEmitter();
  @Output() addMember: EventEmitter<number> = new EventEmitter();
  constructor() { }

  ngOnInit(): void {
  }

  delete(id: number, isSelect: boolean) {
    if (isSelect) {
      this.deleteMember.emit(id);
    }
  }
  add(id: number, isSelect: boolean) {
    if (!isSelect) {
      this.addMember.emit(id);
    }
  }
}
