import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { UserService } from 'src/app/services/user.service';
import { ChatDTO } from 'src/app/model/ChatDTO';
import { ChatService } from 'src/app/services/chat.service';
import { ChatMessageDTO } from 'src/app/model/ChatMessageDTO';
import { CheckUserService } from 'src/app/services/check-user.service';
import { SearchService } from 'src/app/services/search.service';
@Component({
  selector: 'app-left-screen',
  templateUrl: './left-screen.component.html',
  styleUrls: ['./left-screen.component.css'],
})
export class LeftScreenComponent implements OnInit {
  userName: string = localStorage.getItem('user');
  name: string = '';
  public visible = false;
  public visibleAnimate = false;
  listSearch: ChatDTO[] = [];
  constructor(
    private router: Router,
    private userService: UserService,
    private chatService: ChatService,
    private checkUserService: CheckUserService,
    private searchService: SearchService
  ) {}

  ngOnInit(): void {}

  updateStatus(chatDTO: ChatDTO) {
    this.chatService.getListChat().forEach((element) => {
      if (
        element.name != chatDTO.name ||
        (element.name == chatDTO.name && element.isGroup != chatDTO.isGroup)
      ) {
        element.isSelect = false;
      } else {
        element.isSelect = true;
        if (element.messages.length > 0) {
          element.messages[element.messages.length - 1].unRead = false;
        }
        this.chatService.setSelectedChat(element);
      }
    });
  }
  async logout() {
    const result = await this.userService.logout();
    if (result === 'success') {
      this.chatService.clearChatSelected();
      localStorage.removeItem('user');
      this.router.navigateByUrl('login');
      this.userService.webSocketService.closeWebSocket();
    } else {
      alert('err');
    }
  }
  getListChats() {
    return this.chatService.getListChat();
  }
  getLatest(chatDTO: ChatDTO) {
    const length = chatDTO.messages.length;
    if (length == 0) {
      return null;
    } else {
      if (chatDTO.isSelect) {
        chatDTO.messages[length - 1].unRead = false;
      }
      const chatLatest: ChatMessageDTO = Object.assign(
        {},
        chatDTO.messages[length - 1]
      );
      if (chatLatest.sender === localStorage.getItem('user'))
        chatLatest.sender = 'You';
      return chatLatest;
    }
  }

  async search() {
    this.listSearch = [];
    if (this.name === undefined || this.name.trim() === '') {
      alert('You must enter username');
    } else {
      const chatIsGroup: ChatDTO = this.chatService.checkChatInChatList(
        this.name,
        true
      );
      const chatIsNotGroup: ChatDTO = this.chatService.checkChatInChatList(
        this.name,
        false
      );
      // both chats are not in the chat list
      if (chatIsGroup === null && chatIsNotGroup === null) {
        const peopleChat: ChatDTO = await this.searchPeopleChat();
        const groupChat: ChatDTO = await this.searchGroupchat();
        if (peopleChat != null) {
          this.listSearch.push(peopleChat);
        }
        if (groupChat != null) {
          this.listSearch.push(groupChat);
        }
        if (this.listSearch.length == 1) {
          this.chatService.setSelectedChat(this.listSearch[0]);
          this.chatService.addChat(this.listSearch[0]);
          this.updateStatus(this.listSearch[0]);
        } else if (this.listSearch.length == 2) {
          this.show();
        }
      } else if (chatIsGroup !== null && chatIsNotGroup === null) {
        //only group chat is available in the chat list
        const peopleChat: ChatDTO = await this.searchPeopleChat();
        if (peopleChat != null) {
          this.listSearch.push(chatIsGroup);
          this.listSearch.push(peopleChat);
          this.show();
        } else {
          this.updateStatus(chatIsGroup);
        }
      } else if (chatIsGroup === null && chatIsNotGroup !== null) {
        //only people chat is available in the chat list
        const groupChat: ChatDTO = await this.searchGroupchat();
        if (groupChat != null) {
          this.listSearch.push(chatIsNotGroup);
          this.listSearch.push(groupChat);
          this.show();
        } else {
          this.updateStatus(chatIsNotGroup);
        }
      } else {
        // both chats are available in the chat list
        this.listSearch.push(chatIsGroup);
        this.listSearch.push(chatIsNotGroup);
        this.show();
      }
      this.name = '';
    }
  }
  onKey(event: any) {
    this.search();
  }

  async searchPeopleChat(): Promise<ChatDTO> {
    let chat: ChatDTO;
    let listMessages: ChatMessageDTO[] = [];
    let numberPage: number = 1;
    let peopleChat: any = await this.searchService.searchPeopleChat(
      this.name,
      numberPage
    );
    if (peopleChat.status === 'success') {
      // while() to check next page data
      while (peopleChat.data.length !== 0) {
        listMessages = this.getListMessages(peopleChat.data, listMessages);
        numberPage++;
        peopleChat = await this.searchService.searchPeopleChat(
          this.name,
          numberPage
        );
      }
      const status: boolean =
        (await this.checkUserService.checkUser(this.name)) === true;
      chat = {
        name: this.name,
        messages: listMessages,
        isGroup: false,
        isSelect: false,
        members: [],
        status: status,
      };
    } else {
      return null;
    }
    return chat;
  }
  async searchGroupchat(): Promise<ChatDTO> {
    let chat: ChatDTO;
    let numberPage: number = 1;
    let listMessages: ChatMessageDTO[] = [];
    let groupChat: any = await this.searchService.searchGroupChat(
      this.name,
      numberPage
    );
    // while() to check next page data
    if (groupChat.status === 'success') {
      while (groupChat.data.chatData.length !== 0) {
        listMessages = this.getListMessages(
          groupChat.data.chatData,
          listMessages
        );
        numberPage++;
        groupChat = await this.searchService.searchGroupChat(
          this.name,
          numberPage
        );
      }
      const members: string[] = this.getListMembers(groupChat.data);
      let status = false;
      // check group online
      for (const member of members) {
        if (localStorage.getItem('user') === member) continue;
        const s = await this.checkUserService.checkUser(member);
        if (s) {
          status = true;
          break;
        }
      }
      chat = {
        name: groupChat.data.name,
        messages: listMessages,
        isGroup: true,
        isSelect: false,
        members: members,
        status: status,
      };
    } else {
      return null;
    }
    return chat;
  }

  // select peopleChat or groupChat in modal
  selectSearchItem(chatDTO: ChatDTO) {
    const chat: ChatDTO = this.chatService.checkChatInChatList(
      chatDTO.name,
      chatDTO.isGroup
    );
    // if you select an unavailable chat in chat list
    if (chat === null) {
      this.chatService.setSelectedChat(chatDTO);
      this.chatService.addChat(chatDTO);
    }
    // if you select an available chat in chat list
    this.updateStatus(chatDTO);
    this.listSearch = [];
    this.hide();
    this.name = '';
  }

  getListMessages(
    chatData: any[],
    listMessages: ChatMessageDTO[]
  ): ChatMessageDTO[] {
    chatData.forEach((element) => {
      const chatMessageDTO: ChatMessageDTO = {
        sender: element.name,
        receiver: element.to,
        message: element.mes,
        time: new Date(Date.parse(element.createAt)).toLocaleString('es-ES'),
        unRead: false,
        position: '',
      };
      listMessages.unshift(chatMessageDTO);
    });
    return listMessages;
  }

  getListMembers(userData: any): string[] {
    const members: string[] = [userData.own];
    userData.userList.forEach((element) => {
      members.push(element.name);
    });
    return members;
  }

  // modal
  public show(): void {
    this.visible = true;
    setTimeout(() => (this.visibleAnimate = true), 100);
  }

  public hide(): void {
    this.visibleAnimate = false;
    setTimeout(() => (this.visible = false), 20);
  }
  public onContainerClicked(event: MouseEvent): void {
    if ((<HTMLElement>event.target).classList.contains('contentSearchModal')) {
      this.hide();
    }
  }
}
