import { Component, ElementRef, Input, Output, OnInit, EventEmitter } from '@angular/core';
import { RoomService } from 'src/app/services/room.service';
import { ChatDTO } from 'src/app/model/ChatDTO';

import { ViewChild } from '@angular/core';
import { ChatService } from 'src/app/services/chat.service';
@Component({
  selector: 'app-create-chat-group',
  templateUrl: './create-chat-group.component.html',
  styleUrls: ['./create-chat-group.component.css']
})
export class CreateChatGroupComponent implements OnInit {
  @ViewChild('gName')
  myInputVariable: ElementRef;
  @Output() newGroupEvent: EventEmitter<ChatDTO> = new EventEmitter();
  groupName = '';
  public visible = false;
  public visibleAnimate = false;
  

  // modal
  public show(): void {
    this.visible = true;
    setTimeout(() => this.visibleAnimate = true, 100);
  }

  public hide(): void {
    this.visibleAnimate = false;
    this.arrMember = [];
    this.arrMemberChoose.forEach(e => e.isSelect = false);
    setTimeout(() => this.visible = false, 20);
  }
  public onContainerClicked(event: MouseEvent): void {
    if ((<HTMLElement>event.target).classList.contains('modal')) {
      this.hide();
    }
  }
  // reset input
  reset() {
    this.myInputVariable.nativeElement.value = "";
  }

  arrMemberChoose = [
    { id: 0, isSelect: false, isSelectUser: 0, avatar: '../../../assets/image/avatar.png', name: "Minh Nht", message: 'You: How are you?', time: '' },
    { id: 1, isSelect: false, isSelectUser: 1, avatar: '../../../assets/image/avatar.png', name: "Huy Phc", message: 'You: How are you?', time: '10:23' },
    { id: 2, isSelect: false, isSelectUser: 2, avatar: '../../../assets/image/avatar.png', name: "Thanh Tn", message: 'You: How are you?', time: '6.6.21' },
    { id: 3, isSelect: false, isSelectUser: 1, avatar: '../../../assets/image/avatar.png', name: "Loan Loan Loan", message: 'You: How are you?', time: '10:23' },
    { id: 4, isSelect: false, isSelectUser: 2, avatar: '../../../assets/image/avatar.png', name: "Thanh Hoa Hoa", message: 'You: How are you?', time: '6.6.21' },
  ];
  arrMember = [];
  length: string;

  constructor(private roomService: RoomService, private chatService: ChatService) { }

  ngOnInit(): void {

  }
  // tao group
  async createGroupChat(){
    if(this.groupName.trim() === "") {
      alert("You must enter group name")
      return;
    }
    const result: any = await this.roomService.createGroupChat(this.groupName);
    if (result.status ==='success') {
      const chat: ChatDTO ={
        name: result.data.name,
        messages: result.data.chatData,
        isGroup: true,
        isSelect: false,
        members: [localStorage.getItem("user")],
        status: false
      }
      this.chatService.addChat(chat);
      alert('Create '+ this.groupName + ' success!');
      this.updateStatus(chat.name)

      this.reset();
      this.hide();
    } else {
      alert(this.groupName + ' group has been created!');
      this.reset();
    }
  }
  onKey(event: any) {
    this.createGroupChat();
  }

  updateStatus(name: string) {
    this.chatService.getListChat().forEach(element => {
      if (element.name != name) {
        element.isSelect = false;
      } else {
        element.isSelect = true;
        this.chatService.setSelectedChat(element);
      }
    });
  }

  deleteMember(id: number) {
    const indexOfArrMember = this.arrMember.findIndex(member => member.id === id);
    this.arrMember.splice(indexOfArrMember, 1);
    const indexOfArrMemberChoose = this.arrMemberChoose.findIndex(member => member.id === id);
    this.arrMemberChoose[indexOfArrMemberChoose].isSelect = false;
  }

  addMember(id: number) {
    this.arrMemberChoose.forEach(e => {
      if (e.id === id) {
        e.isSelect = true;
        this.arrMember.unshift(e);
        this.length = (this.arrMember.length * 120).toString() + "px";
      }
    })
  }
}
