import { Component, Input, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { UserDTO } from 'src/app/model/UserDTO';
import { UserService } from 'src/app/services/user.service';
import { WebSocketService } from 'src/app/services/web-socket.service';
@Component({
  selector: 'app-registerform',
  templateUrl: './registerform.component.html',
  styleUrls: ['./registerform.component.css'],
})
export class RegisterformComponent implements OnInit {
  alert_username: boolean = false;
  alert_password: boolean = false;
  alert_space: boolean = false;
  alert_confirm: boolean = false;
  alert_wronguser: boolean = false;

  register_form = new FormGroup({
    username: new FormControl(),
    password: new FormControl(),
    confirm_password: new FormControl(),
  });
  private _validator: any;
  constructor(
    private router: Router,
    private userService: UserService,
    private webSocketService: WebSocketService
  ) { }

  ngOnInit(): void {
    const username = localStorage.getItem('user');
    if (username !== null) {
      this.router.navigateByUrl('chat');
    } else {
      if (this.webSocketService.webSocket === undefined || this.webSocketService.webSocket.readyState == 3 || this.webSocketService.webSocket.readyState == 2) {
        this.webSocketService.openWebSocket();
      }
    }
  }
  async signUp() {
    let username =
      this.register_form.value.username == null
        ? ''
        : this.register_form.value.username;
    let password =
      this.register_form.value.password == null
        ? ''
        : this.register_form.value.password;
    let confirm_password =
      this.register_form.value.confirm_password == null
        ? ''
        : this.register_form.value.confirm_password;
    if (username.trim() === '') {
      this.onErrorUsername();
    } else if (password.length < 6) {
      this.onErrorPassword();
    } else if (password.includes(' ')) {
      this.onErrorSpace();
    } else if (password !== confirm_password) {
      this.onErrorConfirm();
    } else {
      const user: UserDTO = {
        username: username,
        password: password,
      };
      const result = await this.userService.checkRegister(user);
      if (result === 'success') {
        const resultLogin = await this.userService.checkLogin(user);
        if (resultLogin['status'] === 'success') {
          localStorage.setItem('code', resultLogin['data']['RE_LOGIN_CODE'])
          localStorage.setItem('user', username);
          this.webSocketService.addListChats(username, []);
          this.router.navigateByUrl('chat');
        } else {
          // alert("wrong ")
        }
      } else {
        this.onErrorUser();
        this.register_form.reset();
      }
    }
  }

  returnLogin() {
    this.router.navigateByUrl('/');
  }
  // Username = null or username = ''
  onErrorUsername() {
    this.alert_username = true;
    setTimeout(() => {
      this.alert_username = false;
    }, 4000);
  }
  // Password.length >= 6
  onErrorPassword() {
    this.alert_password = true;
    setTimeout(() => {
      this.alert_password = false;
    }, 4000);
  }
  // Password has space
  onErrorSpace() {
    this.alert_space = true;
    setTimeout(() => {
      this.alert_space = false;
    }, 4000);
  }
  // Password !== confirmpassword
  onErrorConfirm() {
    this.alert_confirm = true;
    setTimeout(() => {
      this.alert_confirm = false;
    }, 4000);
  }
  // User already exists
  onErrorUser() {
    this.alert_wronguser = true;
    setTimeout(() => {
      this.alert_wronguser = false;
    }, 4000);
  }
}
