import { Injectable } from '@angular/core';
import { ChatDTO } from '../model/ChatDTO';
import { ChatMessageDTO } from '../model/ChatMessageDTO';
import { WebSocketService } from './web-socket.service';

@Injectable({
  providedIn: 'root',
})
export class SearchService {
  constructor(public webSocketService: WebSocketService) {}

  async searchGroupChat(groupName: string, page: number) {
    await this.webSocketService.sendMessage(
      JSON.stringify({
        action: 'onchat',
        data: {
          event: 'GET_ROOM_CHAT_MES',
          data: {
            name: groupName,
            page: page,
          },
        },
      })
    );
    return new Promise((resolve) => {
      const res = JSON.parse(this.webSocketService.res);
      resolve(res);
    });
  }

  async searchPeopleChat(username: string, page: number) {
    await this.webSocketService.sendMessage(
      JSON.stringify({
        action: 'onchat',
        data: {
          event: 'GET_PEOPLE_CHAT_MES',
          data: {
            name: username,
            page: page,
          },
        },
      })
    );
    return new Promise((resolve) => {
      const res = JSON.parse(this.webSocketService.res);
      resolve(res);
    });
  }

  async getUserListChat(data: any): Promise<ChatDTO[]> {
    const result: ChatDTO[] = [];
    for (let i = 0; i < data.length; i++) {
      const element = data[i];
      let chatDTO: ChatDTO;
      let pageNumber = 1;
      if (element['type'] === 0) {
        let messages = await this.searchPeopleChat(element['name'], pageNumber);
        let listMessages: ChatMessageDTO[] = [];
        if (messages['status'] === 'success') {
          while (messages['data'].length != 0) {
            listMessages = this.getListMessage(messages['data'], listMessages);
            pageNumber++;
            messages = await this.searchPeopleChat(element['name'], pageNumber);
          }
          chatDTO = {
            name: element['name'],
            messages: listMessages,
            isGroup: false,
            isSelect: false,
            members: [],
            status: false,
          };
          result.push(chatDTO);
        }
      } else if (element['type'] === 1) {
        let messages = await this.searchGroupChat(element['name'], pageNumber);
        let listMessages: ChatMessageDTO[] = [];
        if (messages['status'] === 'success') {
          const members: string[] = this.getListMembers(messages['data']);
          while (messages['data']['chatData'].length != 0) {
            listMessages = this.getListMessage(
              messages['data']['chatData'],
              listMessages
            );
            pageNumber++;
            messages = await this.searchGroupChat(element['name'], pageNumber);
          }
          chatDTO = {
            name: element['name'],
            messages: listMessages,
            isGroup: true,
            isSelect: false,
            members: members,
            status: false,
          };
          result.push(chatDTO);
        }
      }
    }
    return result;
  }

  getListMessage(data: any, listMessages: ChatMessageDTO[]): ChatMessageDTO[] {
    data.forEach((element) => {
      const chatMessageDTO: ChatMessageDTO = {
        sender: element.name,
        receiver: element.to,
        message: element.mes,
        time: new Date(Date.parse(element.createAt)).toLocaleString('es-ES'),
        unRead: false,
        position: '',
      };
      listMessages.unshift(chatMessageDTO);
    });
    return listMessages;
  }

  getListMembers(userData: any): string[] {
    const members: string[] = [userData.own];
    userData.userList.forEach((element) => {
      members.push(element.name);
    });
    return members;
  }
}
