import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { ChatDTO } from 'src/app/model/ChatDTO';
import { ChatService } from 'src/app/services/chat.service';
import { RoomService } from 'src/app/services/room.service';
import { ChatMessageDTO } from 'src/app/model/ChatMessageDTO';
import { CheckUserService } from 'src/app/services/check-user.service';
import { SearchService } from 'src/app/services/search.service';

@Component({
  selector: 'app-join-group',
  templateUrl: './join-group.component.html',
  styleUrls: ['./join-group.component.css'],
})
export class JoinGroupComponent implements OnInit {
  @ViewChild('gName') myInputVariable: ElementRef;
  groupName = '';
  public visible = false;
  public visibleAnimate = false;
  constructor(
    private roomService: RoomService,
    private chatService: ChatService,
    private checkUserService: CheckUserService,
    private searchService: SearchService
  ) {}

  ngOnInit(): void {}
  // modal
  public show(): void {
    this.visible = true;
    setTimeout(() => (this.visibleAnimate = true), 100);
  }

  public hide(): void {
    this.visibleAnimate = false;
    setTimeout(() => (this.visible = false), 20);
  }
  public onContainerClicked(event: MouseEvent): void {
    if ((<HTMLElement>event.target).classList.contains('modal')) {
      this.hide();
    }
  }
  // reset input
  reset() {
    this.myInputVariable.nativeElement.value = '';
    this.groupName = '';
  }
  async joinGroupChat() {
    if (this.groupName.trim() === '') {
      alert('You must enter group name!');
      return;
    }
    let numberPage = 2;
    let listMessages: ChatMessageDTO[] = [];
    const result: any = await this.roomService.joinGroupChat(this.groupName);
    console.log(result)
    if (result.status === 'success') {
      listMessages = this.getListMessages(result.data.chatData, listMessages);
      let groupChat: any = await this.searchService.searchGroupChat(
        this.groupName,
        numberPage
      );
      if (groupChat.status === 'success') {
        // while() to check next page data
        while (groupChat.data.chatData.length !== 0) {
          listMessages = this.getListMessages(
            groupChat.data.chatData,
            listMessages
          );
          numberPage++;
          groupChat = await this.searchService.searchGroupChat(
            this.groupName,
            numberPage
          );
        }
      }
      const members: string[] = this.getListMembers(result.data);
      let status = false;
      // check group online
      for (const member of members) {
        if (localStorage.getItem('user') === member) continue;
        const s = await this.checkUserService.checkUser(member);
        if (s) {
          status = true;
          break;
        }
      }

      const chat: ChatDTO = {
        name: result.data.name,
        messages: listMessages,
        isGroup: true,
        isSelect: false,
        members: members,
        status: status,
      };

      if (
        this.chatService.checkChatInChatList(chat.name, chat.isGroup) !== null
      ) {
        alert('You have joined to ' + this.groupName + ' group!');
        this.reset();
      } else {
        this.chatService.addChat(chat);
        alert('Join ' + this.groupName + ' success!');
        this.reset();
        this.hide();
      }
      this.updateStatus(chat);
    } else {
      alert(this.groupName + ' group not found!');
      this.reset();
    }
  }

  onKey(event: any) {
    this.joinGroupChat();
  }

  updateStatus(chatDTO: ChatDTO) {
    this.chatService.getListChat().forEach((element) => {
      if (
        element.name != chatDTO.name ||
        (element.name == chatDTO.name && element.isGroup != chatDTO.isGroup)
      ) {
        element.isSelect = false;
      } else {
        element.isSelect = true;
        this.chatService.setSelectedChat(element);
      }
    });
  }

  getListMessages(
    chatData: any[],
    listMessages: ChatMessageDTO[]
  ): ChatMessageDTO[] {
    chatData.forEach((element) => {
      const chatMessageDTO: ChatMessageDTO = {
        sender: element.name,
        receiver: element.to,
        message: element.mes,
        time: new Date(Date.parse(element.createAt)).toLocaleString('es-ES'),
        unRead: false,
        position: '',
      };
      listMessages.unshift(chatMessageDTO);
    });
    return listMessages;
  }

  getListMembers(userData: any): string[] {
    const members: string[] = [userData.own];
    userData.userList.forEach((element) => {
      members.push(element.name);
    });
    return members;
  }
}
