import { Injectable } from '@angular/core';
import { ChatDTO } from '../model/ChatDTO';
import { ChatMessageDTO } from '../model/ChatMessageDTO';
import { ChatStorage } from '../model/ChatStorage';
@Injectable({
  providedIn: 'root',
})
export class WebSocketService {
  webSocket: WebSocket;
  res: string;
  chatStorage: ChatStorage = {};

  constructor() {}

  public openWebSocket() {
    this.webSocket = new WebSocket('ws://203.113.148.132:23023/chat/chat');

    this.webSocket.onopen = (event) => {
      console.log('Open', event);
    };

    this.webSocket.onclose = (event) => {
      console.log('Close', event);
    };
  }

  public sendMessage(message: string) {
    this.webSocket.send(message);

    return new Promise((resolve) => {
      this.webSocket.onmessage = (event) => {
        const response = JSON.parse(event.data);
        if (response.event === 'SEND_CHAT') {
          const isGroup: boolean = response.data.type === 0 ? false : true;
          const chatMessage: ChatMessageDTO = {
            sender: response.data.name,
            receiver: response.data.to,
            message: response.data.mes,
            time: new Date().toLocaleString('es-ES'),
            unRead: true,
            position: ""
          };
          this.addChatMessage(chatMessage, false, isGroup);
        }
        this.res = event.data;
        resolve(this.res);
      };
    });
  }

  public sendChatMessage(message: string) {
    this.webSocket.send(message);
  }

  public closeWebSocket() {
    this.webSocket.close();
  }
  async searchGroupChat(groupName: string, page: number) {
    await this.sendMessage(
      JSON.stringify({
        action: 'onchat',
        data: {
          event: 'GET_ROOM_CHAT_MES',
          data: {
            name: groupName,
            page: page,
          },
        },
      })
    );
    return new Promise((resolve) => {
      const res = JSON.parse(this.res);
      resolve(res);
    });
  }

  async searchPeopleChat(groupName: string, page: number) {
    await this.sendMessage(
      JSON.stringify({
        action: 'onchat',
        data: {
          event: 'GET_PEOPLE_CHAT_MES',
          data: {
            name: groupName,
            page: page,
          },
        },
      })
    );
    return new Promise((resolve) => {
      const res = JSON.parse(this.res);
      resolve(res);
    });
  }
  async addChatMessage(
    chatMessage: ChatMessageDTO,
    isSend: boolean,
    isGroup: boolean
  ) {
    let name;
    if (isGroup) {
      name = chatMessage.receiver;
    } else {
      name = isSend ? chatMessage.receiver : chatMessage.sender;
    }
    const hasChat = this.hasChat(name, isGroup);
    if (hasChat) {
      let chat: ChatDTO;
      this.chatStorage[this.getUserSession()].forEach((e) => {
        if (e.name.toLowerCase() === name.toLowerCase() && e.isGroup === isGroup) {
          e.messages.push(chatMessage);
          chat = e;
          const index = this.chatStorage[this.getUserSession()].indexOf(e);
          this.chatStorage[this.getUserSession()].splice(index, 1);
          return;
        }
      });
      this.chatStorage[this.getUserSession()].unshift(chat);
    } else {
      let chatDTO: ChatDTO = {
        name: name,
        messages: [chatMessage],
        isGroup: isGroup,
        isSelect: isSend ? true : false,
        members: [],
        status: false,
      };
      // trick
      if (!chatDTO.isGroup) {
        await this.searchPeopleChat(chatDTO.name, 1);
      }
      if (chatDTO.isGroup){
        await this.searchGroupChat(chatDTO.name, 1);
      }
      chatDTO = await this.updateNewChat(chatDTO);
      this.chatStorage[this.getUserSession()].unshift(chatDTO);
    }
  }

  private hasChat(name: string, isGroup: boolean) {
    for (const element of this.chatStorage[this.getUserSession()])
      if (
        element.name.toLowerCase() === name.toLowerCase() &&
        element.isGroup === isGroup
      )
        return true;
    return false;
  }

  private getUserSession(): string {
    return localStorage.getItem('user');
  }

  public getListChats(): ChatDTO[] {
    return this.chatStorage[this.getUserSession()];
  }

  public addListChats(username: string, chatDTOs: ChatDTO[]) {
    // if (this.chatStorage[username] === undefined) {
      this.chatStorage[username] = chatDTOs;
    // }
  }

  async updateNewChat(chatDTO: ChatDTO): Promise<ChatDTO> {
    let numberPage = 1;
    let listMessages: ChatMessageDTO[] = [];
    if (chatDTO.isGroup) {
      let groupChat: any = await this.searchGroupChat(chatDTO.name, numberPage);
      if (groupChat.status === 'success') {
        // while() to check next page data
        while (groupChat.data.chatData.length !== 0) {
          listMessages = this.getListMessages(
            groupChat.data.chatData,
            listMessages
          );
          numberPage++
          groupChat = await this.searchGroupChat(chatDTO.name, numberPage);
        }
        const members: string[] = this.getListMembers(groupChat.data);
        listMessages[listMessages.length - 1].unRead = true;
        chatDTO.messages = listMessages;
        chatDTO.members = members;
      }
    } else {
      let peopleChat: any = await this.searchPeopleChat(
        chatDTO.name,
        numberPage
      );
      if (peopleChat.status === 'success') {
      // while() to check next page data
      while (peopleChat.data.length !== 0) {
        listMessages = this.getListMessages(peopleChat.data, listMessages);
        numberPage++
        peopleChat = await this.searchPeopleChat(chatDTO.name, numberPage);
      }

        listMessages[listMessages.length - 1].unRead = true;
        chatDTO.messages = listMessages;
      }
    }
    return chatDTO;
  }

  getListMessages(
    chatData: any[],
    listMessages: ChatMessageDTO[]
  ): ChatMessageDTO[] {
    chatData.forEach((element) => {
      const chatMessageDTO: ChatMessageDTO = {
        sender: element.name,
        receiver: element.to,
        message: element.mes,
        time: new Date(Date.parse(element.createAt)).toLocaleString('es-ES'),
        unRead: false,
        position: ""
      };
      listMessages.unshift(chatMessageDTO);
    });
    return listMessages;
  }

  getListMembers(userData: any): string[] {
    const members: string[] = [userData.own];
    userData.userList.forEach((element) => {
      members.push(element.name);
    });
    return members;
  }
}
