import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { Router } from '@angular/router';
import { ChatDTO } from 'src/app/model/ChatDTO';
import { UserDTO } from 'src/app/model/UserDTO';
import { ChatService } from 'src/app/services/chat.service';
import { SearchService } from 'src/app/services/search.service';
import { UserService } from 'src/app/services/user.service';
import { WebSocketService } from 'src/app/services/web-socket.service';

@Component({
  selector: 'app-loginform',
  templateUrl: './loginform.component.html',
  styleUrls: ['./loginform.component.css'],
})
export class LoginformComponent implements OnInit {
  alert_username: boolean = false;
  alert_password: boolean = false;
  alert_space: boolean = false;
  alert: boolean = false;
  login_form = new FormGroup({
    username: new FormControl(),
    password: new FormControl(),
  });
  ngOnInit(): void {
    const username = localStorage.getItem('user');
    if (username !== null) {
      this.router.navigateByUrl('chat');
    } else {
      if (this.webSocketService.webSocket === undefined || this.webSocketService.webSocket.readyState == 3 || this.webSocketService.webSocket.readyState == 2) {
        this.webSocketService.openWebSocket();
      }
    }
  }

  constructor(
    private router: Router,
    private webSocketService: WebSocketService,
    private userService: UserService,
    private chatService: ChatService,
    private searchService: SearchService
  ) { }

  async login() {
    let username =
      this.login_form.value.username == null
        ? ' '
        : this.login_form.value.username;
    let password =
      this.login_form.value.password == null
        ? ' '
        : this.login_form.value.password;
    if (username.trim() === '') {
      this.onErrorUsername();
    } else if (password.length < 6) {
      this.onErrorPassword();
    } else if (password.includes(' ')) {
      this.onErrorSpace();
    } else {
      const user: UserDTO = {
        username: username,
        password: password,
      };
      
      const result : any = await this.userService.checkLogin(user);
      if (result.status === 'success') {
        localStorage.setItem('user', username);
        localStorage.setItem('code', result.data.RE_LOGIN_CODE)
        const userList = await this.chatService.getUserList();
        const chatDTOs: ChatDTO[] = await this.searchService.getUserListChat(userList['data']);
        this.webSocketService.addListChats(username, chatDTOs);
        this.router.navigateByUrl('chat');
      } else {
        this.onError();
        this.login_form.reset();
      }
    }
  }
  register(): void {
    this.router.navigateByUrl('register');
  }
  forgotpassword(): void {
    this.router.navigateByUrl('forgotpassword');
  }
  onError() {
    this.alert = true;
    setTimeout(() => {
      this.alert = false;
    }, 4000);
  }
  // Username = null or username = ''
  onErrorUsername() {
    this.alert_username = true;
    setTimeout(() => {
      this.alert_username = false;
    }, 4000);
  }
  // Password.length >= 6
  onErrorPassword() {
    this.alert_password = true;
    setTimeout(() => {
      this.alert_password = false;
    }, 4000);
  }
  // Password has space
  onErrorSpace() {
    this.alert_space = true;
    setTimeout(() => {
      this.alert_space = false;
    }, 4000);
  }
}
