import { Component, OnDestroy, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { interval, Subscription } from 'rxjs';
import { ChatDTO } from 'src/app/model/ChatDTO';
import { ChatService } from 'src/app/services/chat.service';
import { CheckUserService } from 'src/app/services/check-user.service';
import { SearchService } from 'src/app/services/search.service';
import { UserService } from 'src/app/services/user.service';
import { WebSocketService } from 'src/app/services/web-socket.service';

@Component({
  selector: 'app-chat-screen',
  templateUrl: './chat-screen.component.html',
  styleUrls: ['./chat-screen.component.css'],
})
export class ChatScreenComponent implements OnInit, OnDestroy {
  private subcription: Subscription;
  constructor(
    private router: Router,
    private checkUserService: CheckUserService,
    private webSocketService: WebSocketService,
    private userService: UserService,
    private chatService: ChatService,
    private searchService: SearchService
  ) {
    this.subcription = interval(5000).subscribe((x) => {
      checkUserService.updateStatus();
    });
  }
  ngOnDestroy(): void {
    this.subcription.unsubscribe();
  }

  ngOnInit(): void {
    if (
      this.webSocketService.webSocket === undefined ||
      this.webSocketService.webSocket.readyState !== 1
    ) {
      this.webSocketService.openWebSocket();

      const itv =  setInterval(() => {
        if(this.webSocketService.webSocket.readyState == 1) {
          this.reLogin();
          clearInterval(itv);
        }
      }, 500)
    }
  }
  async reLogin() {
    const username = localStorage.getItem('user');
    const code = localStorage.getItem('code');
    const result: any = await this.userService.checkReLogin(username, code);
    if (result.status === 'success') {
      localStorage.setItem('code', result.data.RE_LOGIN_CODE);
      const userList = await this.chatService.getUserList();
      const chatDTOs: ChatDTO[] = await this.searchService.getUserListChat(userList['data']);
      this.webSocketService.addListChats(username, chatDTOs);
    } else {
      localStorage.removeItem('user');
      localStorage.removeItem('code');
      this.router.navigateByUrl('login');
    }
  }
}
