import { Component, Input, OnInit, Output, EventEmitter  } from '@angular/core';
@Component({
  selector: 'app-member-name',
  templateUrl: './member-name.component.html',
  styleUrls: ['./member-name.component.css']
})
export class MemberNameComponent implements OnInit {
  @Input() name: string;
  @Input() id: number;
  @Output() deleteMember: EventEmitter<number> = new EventEmitter();
  constructor() { }

  ngOnInit(): void {
  }
  delete(id: number){
    console.log(id);
    this.deleteMember.emit(id);
  }
}
