import { Injectable } from '@angular/core';
import { UserDTO } from '../model/UserDTO';
import { WebSocketService } from './web-socket.service';

@Injectable({
  providedIn: 'root',
})
export class UserService {

  constructor(public webSocketService: WebSocketService) {
  }

  private getMessage(username: string, password: string) {
    return {
      action: 'onchat',
      data: {
        event: 'LOGIN',
        data: {
          user: username,
          pass: password,
        },
      },
    };
  }

  private getRegister(username: string, password: string) {
    return {
      action: "onchat",
      data: {
        event: "REGISTER",
        data: {
          user: username,
          pass: password
        }
      }
    }
  }
  private getReLogin(username : string, code : string){
    return {
      action: "onchat",
      data: {
        event: "RE_LOGIN",
        data: {
          user: username,
          code: code
        }
      }
    }
  }

  async checkLogin(user: UserDTO) {
    await this.webSocketService.sendMessage(
      JSON.stringify(this.getMessage(user.username, user.password))
    );
    return new Promise((resolve) => {
      const res = JSON.parse(this.webSocketService.res);
      resolve(res);
    });
  }
  
  async checkReLogin(username :string , code : string){
    await this.webSocketService.sendMessage(
      JSON.stringify(this.getReLogin(username, code))
    )
    return new Promise((resolve) => {
      const res = JSON.parse(this.webSocketService.res);
      resolve(res);
    });
  }

  async checkRegister(user: UserDTO) {
    await this.webSocketService.sendMessage(
      JSON.stringify(this.getRegister(user.username, user.password))
    );
    return new Promise((resolve) => {
      const res = JSON.parse(this.webSocketService.res);
      resolve(res.status);
    });
  }

  async logout() {
    await this.webSocketService.sendMessage(
      JSON.stringify({
        action: 'onchat',
        data: {
          event: 'LOGOUT'
        }
      })
    );
    return new Promise((resolve) => {
      const res = JSON.parse(this.webSocketService.res);
      resolve(res.status);
    });
  }
}