import { Injectable } from '@angular/core';
import { WebSocketService } from './web-socket.service';
@Injectable({
  providedIn: 'root',
})
export class RoomService {
  constructor(public webSocketService: WebSocketService) { }

  async createGroupChat(groupName: String) {
    await this.webSocketService.sendMessage(
      JSON.stringify({
        action: 'onchat',
        data: {
          event: 'CREATE_ROOM',
          data: {
            name: groupName,
          },
        },
      })
    );
    return new Promise((resolve) => {
      const res = JSON.parse(this.webSocketService.res);
      resolve(res);
    });
  }

  async joinGroupChat(groupName: string) {
    await this.webSocketService.sendMessage(
      JSON.stringify({
        action: 'onchat',
        data: {
          event: 'JOIN_ROOM',
          data: {
            name: groupName,
          },
        },
      })
    );
    return new Promise((resolve) => {
      const res = JSON.parse(this.webSocketService.res);
      resolve(res);
    });
  }
}
